<template lang="pug">

.catalog
	v-categories
	v-list
	v-cart

</template>
<script>
import vCategories from './categories'
import vList from './list'
import vCart from './cart'
export default {
	beforeCreate() {
		if (!this.$store.getters['catalog/loaded'])
			this.$store.dispatch('catalog/load')
	},
	components: {
		vCategories,
		vList,
		vCart
	}
}
</script>
<style lang="stylus" scoped>

.catalog
	display flex
	align-items flex-start
	justify-content center
	padding 0 100px

</style>