<template lang="pug">

.category(@click="toggleCategory" :class="{ active, big }") {{ category.name }}
	.hidden-text {{ category.name }}

</template>
<script>
export default {
	data: () => ({
		big: false
	}),
	methods: {
		toggleCategory(){
			this.$emit('toggleCategory')
		}
	},
	computed: {
		name() {
			return this.category.name
		}
	},
	watch: {
		name: {
			immediate: true,
			async handler(text) {
				await this.$nextTick()
				if (this.$el.querySelector('.hidden-text').offsetWidth > 90) this.big = true
				else this.big = false
			}
		}
	},
	props: {
		category: {
			type: Object
		},
		active: {
			type: Boolean
		}
	},
}
</script>
<style lang="stylus" scoped>

.category
	font-size 15px
	color #8E8E93
	background: #F8F9F8
	box-shadow: 0px 3px 6px rgba(204, 191, 171, 0.6)
	border-radius: 8px
	margin-right 20px
	display flex
	align-items center
	justify-content center
	margin-bottom 20px
	cursor pointer
	transition color .1s, box-shadow .2s
	user-select none
	width 90px
	height 60px
	text-align center
	display inline-flex
	&.big
		width 200px
	&:hover
		box-shadow: 0px 2px 5px rgba(204, 191, 171, 0.6)
	&:active
		box-shadow: 0px 2px 3px rgba(204, 191, 171, 0.3)
	&.active
		color $green
	&.show-enter-active
		animation show .5s
	&.show-leave-active
		animation show .5s reverse

.hidden-text
	font-size 15px
	position fixed
	display inline
	left -500px
	top 0
	opacity 0
	z-index -2

@keyframes show
	0%
		display none
		position absolute
		transform scale(0)
	49%
		display none
		position absolute
		transform scale(0)
	51%
		display block
		position relative
		transform scale(0)
	100%
		display block
		position relative
		transform scale(1)

</style>