<template lang="pug">

.cart
	v-navbar
	
	.products
		.column(v-for="(column, index) in masonry" :key="index")
			v-product(v-for="number in column" :product="cart[number]" :key="number")
	.buy(v-if="cart.length > 0")
		.count Кол-во наименований: {{ $store.getters['catalog/cartCount'] }}
		.sum Итого: {{ $store.getters['catalog/sum'] }} руб
		.btn
			span Оформить заказ

</template>
<script>
import vProduct from '../product'
import vNavbar from './navbar'
export default {
	data: () => ({
		height: 0
	}),
	components: {
		vNavbar,
		vProduct
	},
	computed: {
		cart() {
			return this.$store.getters['catalog/cart']
		},
		masonry() {
			let masonry = [[],[]]

			for (const index in this.cart)
				masonry[index%2].push(index)

			return masonry
		}
	},
}
</script>
<style lang="stylus">
.title
	svg
		path
			fill #FEA472
</style>
<style lang="stylus" scoped>

.cart
	width 50%
	width 330px
	flex-shrink 0
	position relative

.products
	transition height .3s
	transition height .3s
	display flex
	flex-direction row
	flex-wrap wrap
	align-items flex-start

.column
	margin-right 15px
	width 140px
	&:nth-child(3n+3)
		margin-right 0

.buy
	text-align right
	margin-top 45px
	.sum, .count
		font-size 15px
		font-weight 500
		font-size 24px
		color #61B335
	.btn
		font-weight 500
		font-size 24px
		color #68922B
		background-color #C0EA6A
		height 41px
		border-radius 41px
		box-shadow: 0px 4px 6px rgba(#000, 0.25)
		padding 0 15px
		display inline-flex
		align-items center
		justify-content center
		margin-top 15px
		cursor pointer
		transition box-shadow .2s
		&:hover
			box-shadow: 0px 3px 5px rgba(#000, 0.2)
		&:active
			box-shadow: 0px 2px 4px rgba(#000, 0.15)

</style>