<template lang="pug">

.navbar
	.search
		v-search-icon
		input(v-model="search" placeholder="Найти")
	.sort
		.sort-item(v-for="item in sort" :class="{ active: active.name == item.name && active.direction == item.direction }" @click="active = item")
			.sort-item-ok
				v-icon-ok
			| {{ item.title }}

</template>
<script>
import vSearchIcon from 'icons/search'
export default {
	data: ({ $store }) => ({
		search: $store.state.catalog.server.search,
		sort: [
			{
				title: 'По популярности',
				name: 'popularity',
				direction: -1
			},
			{
				title: 'Сначала дешевле',
				name: 'price',
				direction: 1
			},
			{
				title: 'Сначала дороже',
				name: 'price',
				direction: -1
			},
			{
				title: 'От А до Я',
				name: 'name',
				direction: 1
			},
			{
				title: 'От Я до А',
				name: 'name',
				direction: -1
			},
		],
	}),
	computed: {
		active: {
			get() {
				return this.$store.state.catalog.server.sort
			},
			set({ name, direction }) {
				this.$store.dispatch('catalog/setFilter', {
					name: 'setSort',
					payload: { name, direction }
				})
			}
		},
	},
	watch: {
		search(text) {
			this.$store.dispatch('catalog/setLoading', true)
			setTimeout(() => {
				if (text == this.search)
					this.$store.dispatch('catalog/setFilter', {
						name: 'setSearch',
						payload: text
					})
			}, 1000)
		}
	},
	components: {
		vSearchIcon
	}
}
</script>
<style lang="stylus" scoped>

.navbar
	margin-bottom 20px

.search
	position relative
	height 32px
	height 25px
	width 100%
	background-color #F8F9F8
	border-radius 10px
	display flex
	align-items center
	input
		background-color transparent
		border 0
		width 100%
		height 100%
		padding-left 10px
		font-size 15px
		&::placeholder
			color #8E8E93
	svg
		margin-left 8px
		width 16px

.sort
	display flex
	flex-wrap wrap
	margin-top 13px
	&-item
		box-shadow: 0px 2px 4px rgba(204, 191, 171, 0.6)
		background-color #F8F9F8
		border-radius 5px
		color #8E8E93
		font-size 14px
		padding 3px 5px
		padding-right 7px
		position relative
		cursor pointer
		display flex
		align-items center
		margin-bottom 6px
		margin-right 7px
		&-ok
			position relative
			border-radius: 5px
			width 16px
			height 16px
			margin-right 6px
			background rgba(196, 196, 196, 0.65)
			display flex
			align-items center
			justify-content center
			svg
				opacity 0
				width 10px
				margin-top 1px
				margin-left 1px
		&.active
			.sort-item-ok
				background: #6FC83F
				svg
					display block
					opacity 1


</style>