<template lang="pug">

.units(v-if="package.length > 1" :class="{ expand }" @click="expand = !expand")
	.unit(v-for="(pack, index) in package" :class="{ active: active == index }" @click="setActive(index)") {{ pack == 1 ? '' : pack }} шт
.units.disabled(v-else)
	.unit.active {{ package[0] == 1 ? '' : package[0] }} шт

</template>
<script>
export default {
	data: () => ({
		expand: false
	}),
	props: {
		package: {
			type: Array
		},
		active: {
			type: Number
		}
	},
	methods: {
		setActive(index) {
			if (this.active == index) return
			this.$emit('setUnit', index)
		}
	}
}
</script>
<style lang="stylus" scoped>

.units
	background-color #F8F9F8
	border-radius 5px
	margin-right 3px
	position relative
	top 2px
	display flex
	flex-direction column
	flex-shrink 0
	.unit
		transition height .15s, line-height .15s
		cursor pointer
		font-size 12px
		color #8E8E93
		order 1
		line-height 0
		padding 0 5px
		display flex
		align-items center
		justify-content center
		height 0
		overflow hidden
		&.active
			height 20px
			line-height 20px
			order 0
	&.expand
		.unit
			height 20px
			line-height 20px
	&.disabled
		background-color #fff
		cursor default

</style>