<template lang="pug">

.categories
	.title.active(v-if="children.length > 0" @click="active = parent") {{ active.name }}
	.title.active(v-else-if="parent" @click="active = grandParent") {{ parent.name }}
	.title(v-else) Категории
	transition-group(name="show").container
		v-category(v-for="category in categories" :category="category" :active="active ? category.id == active.id : false" :key="category.id" @toggleCategory="active = category")

</template>
<script>
import vCategory from './category'
export default {
	computed: {
		active: {
			get() {
				return this.$store.getters['catalog/categoryActive']
			},
			set(category) {
				let id = category ? category.id : 0

				if (this.active && this.active.id == id)
					id = this.parent ? this.parent.id : 0

				this.$store.dispatch('catalog/setFilter', {
					name: 'setCategory',
					payload: id
				})
			}
		},
		children() {
			return this.active ? this.$store.getters['catalog/categoryChildren'](this.active) : []
		},
		parent() {
			return this.active ? this.$store.getters['catalog/categoryParent'](this.active) : null
		},
		grandParent() {
			return this.parent ? this.$store.getters['catalog/categoryParent'](this.parent) : null
		},
		siblings() {
			return this.parent ? this.$store.getters['catalog/categoryChildren'](this.parent) : []
		},
		rootCategories() {
			return this.$store.getters['catalog/categoryChildren'](null)
		},
		categories() {
			if (this.children.length > 0) return this.children
			else if (this.parent) return this.siblings
			else return this.rootCategories
		}
	},
	components: {
		vCategory
	},
}
</script>
<style lang="stylus" scoped>

.categories
	width 230px
	padding-right 10px
	display flex
	flex-wrap wrap

.title
	color $pink-red
	font-weight 500
	font-size 22px
	margin-bottom 20px
	user-select none
	&.active
		cursor pointer

</style>