<template lang="pug">

.product(:class="{ focused, added: product.cart }")
	.img
		v-icon-avocado
	.name {{ product.name }}
	.price {{ product.cart ? (product.price * product.count).toFixed(2) : product.price }} руб
	.information(@click="showInformation")
		v-icon-information
	transition(name="show")
		v-calculator(v-if="product.cart" :product="product")
	.toggle(@click="toggleProduct")
		v-icon-plus

</template>
<script>
import vCalculator from './calculator'
export default {
	data: () => ({
		focused: false
	}),
	methods: {
		showInformation() {
			this.$store.dispatch('ui/setPopup', { name: 'product', query: this.product.id })
		},
		toggleProduct() {
			if (this.product.cart)
				this.$store.commit('catalog/removeProductFromCart', this.product)
			else
				this.$store.commit('catalog/setProductCount', { product: this.product, count: 1 })
		},
		focus(event) {
			this.focused = true
		},
		blur(event) {
			this.focused = false
		},
	},
	props: {
		product: {
			type: Object,
			required: true
		},
	},
	components: {
		vCalculator
	}
}
</script>
<style lang="stylus">
.product
	.toggle
		svg
			path
				stroke #FBF5DB
</style>
<style lang="stylus" scoped>

.product
	background-color #FBF5DB
	border-radius 10px
	width 140px
	position relative
	box-shadow: 0px 4px 5px rgba(204, 191, 171, 0.5)
	margin-bottom 15px
	text-align center
	transition margin-bottom .2s, background-color .3s
	padding 13px 5px 10px
	&.added
		margin-bottom 45px
		background-color #FBFBFB
		.price
			color #61B335
			background-color #EBEDEB
		.toggle
			transform rotate(45deg)
			background-color $pink-red

.img
	text-align center
	svg
		width 33px

.name
	margin-top 5px
	font-size 12px
	text-align center
	color $green

.price
	margin-top 8px
	font-size 15px
	text-align center
	color $green
	height 25px
	border-radius 25px
	line-height 25px
	background-color #fff
	display inline-block
	padding 0 12px

.toggle, .information
	z-index 2
	position absolute
	width 20px
	height 20px
	border-radius 100%
	display flex
	align-items center
	justify-content center
	transition box-shadow .2s, background-color .15s, transform .15s
	cursor pointer

.information
	top -6px
	left -6px
	background-color #fff
	border: 1.5px solid #8E8E93
	padding-bottom 1px
	box-shadow: 0px 4px 4px rgba(204, 191, 171, 0.6)
	svg
		width 3px
	&:hover
		box-shadow 0px 4px 6px rgba(204, 191, 171, 0.4)
	&:active
		box-shadow 0px 2px 3px rgba(204, 191, 171, 0.1)

.toggle
	top -5px
	right -5px
	background-color $green
	box-shadow 0px 4px 6px rgba(204, 191, 171, 0.25)
	&:hover
		box-shadow 0px 4px 6px rgba(204, 191, 171, 0.1)
	&:active
		box-shadow 0px 2px 3px rgba(204, 191, 171, 0.1)
	svg
		width 16px

</style>