<template lang="pug">

.list
	v-navbar
	.products(:class="{ loading: this.$store.getters['catalog/loading'] }")
		.column(v-for="(column, index) in masonry" :key="index")
			v-product(v-for="number in column" :product="products[number]" :key="number")

</template>
<script>
import vProduct from '../product'
import vNavbar from './navbar'
export default {
	computed: {
		products() {
			return this.$store.getters['catalog/show']
		},
		length() {
			return this.products.length / 3
		},
		masonry() {
			let masonry = [[],[],[]]

			for (const index in this.products)
				masonry[index%3].push(index)

			return masonry
		}
	},
	components: {
		vNavbar,
		vProduct,
	},
}
</script>
<style lang="stylus" scoped>

.list
	width 450px
	flex-shrink 0
	margin-right 40px
	
.column
	margin-right 15px
	&:nth-child(3n+3)
		margin-right 0

.products
	position relative
	display flex
	flex-wrap wrap
	align-items flex-start
	transition opacity .2s
	min-height 100px
	&::after
		content ''
		position absolute
		top 50%
		left 50%
		width 50px
		height 50px
		margin-left -25px
		margin-top -25px
		border-radius 100%
		border 4px solid #666
		border-bottom 4px solid transparent
		opacity 0
		transition opacity .3s
		animation loader 1s infinite linear
	&.loading
		opacity .4
		&::after
			opacity 1

@keyframes loader
	0%
		transform rotate(0)
	100%
		transform rotate(360deg)

</style>