<template lang="pug">

.calculator
	.minus(@click="value--" :class="{ disabled: value == 0 }")
		v-minus-icon
	.value
		input(v-model="value" @focus="focus" @blur="blur" :style="{ width: width + 'px' }" @input="input")
		v-package(:package="product.package" :active="product.unit" @setUnit="setUnit")
	.plus(@click="value++")
		v-plus-icon
	.hidden-value {{ value }}

</template>
<script>
import vPlusIcon from 'icons/plus'
import vMinusIcon from 'icons/minus'
import vPackage from './package'

export default {
	data: (context) => ({
		width: 0,
		activeUnit: 0
	}),
	computed: {
		value: {
			get() {
				return this.product.count / this.units
			},
			set(value) {
				value += ''
				value = parseInt(value.split(' ').join('').replace(/[^-0-9]/gim, '') || 0)
				if (value == this.value) return
				this.$store.commit('catalog/setProductCount', { product: this.product, count: value * this.units })
			}
		},
		units: {
			get() {
				return this.product.package[this.product.unit]
			},
			set(index) {
				this.product.unit = index
			}
		}
	},
	watch: {
		value: {
			immediate: true,
			async handler(value) {
				await this.$nextTick()
				this.width = this.$el.querySelector('.hidden-value').offsetWidth
			}
		},
	},
	methods: {
		setUnit(index) {
			let value = this.value
			this.units = index
			this.value = value
		},
		focus(event) {
			// console.log(event)
			this.$emit('focus')
		},
		blur(event) {
			// console.log(event)
			this.$emit('blur')
		},
		input(event) {
			event.target.value = this.value
		},
	},
	props: {
		product: {
			type: Object,
			required: true
		}
	},
	components: {
		vPlusIcon,
		vMinusIcon,
		vPackage
	}
}
</script>
<style lang="stylus" scoped>

.calculator
	bottom -32px
	position absolute
	display flex
	align-items center
	justify-content center
	width 100%
	left 0
	z-index 2
	&.show-enter-active
		z-index -1
		animation calcToggle .2s
	&.show-leave-active
		z-index -1
		animation calcToggle .2s reverse

@keyframes calcToggle
	0%
		bottom 0
		transform translateY(-100%)
	100%
		bottom -32px
		transform translateY(0)

.value
	margin 0 8px
	height 25px
	max-width calc(100% - 68px)
	background-color #fff
	box-shadow: 0px 4px 4px rgba(204, 191, 171, 0.6)
	border-radius: 5px
	display flex
	align-items flex-start
	input
		color #8E8E93
		font-size 14px
		min-width 21px
		max-width 100%
		height 100%
		padding 0 6px
		border 0
		background-color transparent
		text-align center
		transition width .2s
		&:focus
			transition none

.hidden-value
	opacity 0
	position fixed
	left -300px
	top 50%
	font-size 16px
	padding 0 6px

.minus, .plus
	width 25px
	height 25px
	display flex
	align-items center
	justify-content center
	border-radius 100%
	box-shadow: 0px 4px 4px rgba(204, 191, 171, 0.6)
	cursor pointer
	user-select none
	transition box-shadow .2s, background-color .2s, opacity .2s
	&.disabled
		cursor default
		background-color #ccc
	&:hover
		box-shadow: 0px 4px 4px rgba(204, 191, 171, 0.4)
	&:active
		box-shadow: 0px 2px 2px rgba(204, 191, 171, 0.1)

.minus
	background-color $pink-red
	svg
		width 16px

.plus
	background-color $green
	svg
		width 18px

</style>